import React, {useState} from 'react'

function ToDo (){
     const id = Math.random;
    const [items,setItems] = useState([]);
    const [task,setTask] = useState('');
    const [index,setIndex] = useState(0);
    
     const handleClick = (e) => {
         e.preventDefault();
         if(task===''){
             alert('Pls enter a non empty task')
             return
         }
         setItems([...items,{task:task,id:index}])
         setIndex(pi => (pi+1))
         setTask('')
     }
     const handleDelete = (e) => {
         setItems([...items].filter(element => element.task != e.target.name))
         setTask('')
     }
     const updateTask = (e) => {
         setTask(e.target.value)
     }
        return(
            <div>
                <main className="mainBar">
                <form onSubmit={handleClick}>
                <input placeholder="Enter the task name" type="text" name="task" value={task} onChange={updateTask} />
                <button className="srcBtn" >Add task</button>
                </form>
                <ul className="itemList">
                    {items.map(item => (<li key={item.id}>{item.task}<button className="delBtn" name={item.task} onClick={handleDelete}>Delete</button></li>))}
                </ul>
                </main>
            </div>
        )
        }  

export default ToDo